input, button {
    padding: 0;
    font: inherit;
    outline: none;
    border: none;
    margin: 0;
}

button {
    cursor: pointer;
}

ul {
    padding: 0;
    list-style: none;
    margin: 0;
}

li {
    display: inline-block;
    position: relative;
}



